  text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.25);
  padding: 2px 4px;
  font-size: 11.844px;
  line-height:14px;
}
.Editor-editor{
  height:300px;
  padding:1%;
  border:1px solid #EEE;
  border-radius:0;
